import { MOONPAY_WIDGET_CONTAINER_ID } from './constants';
import type {
  MoonPayBalanceDepositWidgetConfig,
  MoonPayBalanceWithdrawWidgetConfig,
  MoonPayAccountChallengeConfig,
  MoonPayBuyConfig,
  MoonPayClaimWidgetConfig,
  MoonPayConsumerKycConfig,
  MoonPayDebug,
  MoonPayEnvironment,
  MoonPayLegacyConfig,
  MoonPayNftWidgetConfig,
  MoonPayPartnerTopupConfig,
  MoonPaySellConfig,
  MoonPaySwapConfig,
  MoonPaySwapsCustomerSetupConfig,
  MoonPayTopUpBalanceLedgerWidgetConfig,
  MoonPayTransactionTrackerConfig,
  MoonPayWidgetProps,
  MoonPayWithdrawLedgerWidgetConfig,
  TFlow,
} from './types';

type CommonProps<T extends TFlow> = {
  debug: MoonPayDebug;
  environment: MoonPayEnvironment;
  props: MoonPayWidgetProps<T>;
};

type ConfigConversionParams<T extends TFlow> = T extends
  | 'legacy'
  | 'moonPayBalanceDeposit'
  | 'moonPayBalanceWithdraw'
  ? Omit<CommonProps<T>, 'apiKey' | 'signature'>
  : CommonProps<T> & {
      apiKey: string;
      signature: string;
    };

export const getAccountChallengeConfigFromProps = ({
  debug,
  environment,
  props,
}: ConfigConversionParams<
  'accountChallenge'
>): MoonPayAccountChallengeConfig => {
  const { baseOrigin, challengeId, onChallengeCompleted, variant } = props;

  const params: MoonPayAccountChallengeConfig['params'] = {
    challengeId,
  };

  const handlers: MoonPayAccountChallengeConfig['handlers'] = {
    onChallengeCompleted,
  };

  return {
    flow: 'accountChallenge',
    environment,
    variant,
    baseOrigin,
    handlers,
    params,
    debug,
  };
};

export const getBuyConfigFromProps = ({
  props,
  apiKey,
  debug,
  environment,
  signature,
}: ConfigConversionParams<'buy'>): MoonPayBuyConfig => {
  const {
    // params
    currencyCode,
    defaultCurrencyCode,
    walletAddress,
    walletAddressTag,
    walletAddresses,
    walletAddressTags,
    colorCode,
    theme,
    themeId,
    language,
    baseCurrencyCode,
    baseCurrencyAmount,
    quoteCurrencyAmount,
    lockAmount,
    email,
    externalTransactionId,
    externalCustomerId,
    paymentMethod,
    redirectURL,
    showAllCurrencies,
    showOnlyCurrencies,
    showWalletAddressForm,
    unsupportedRegionRedirectUrl,
    skipUnsupportedRegionScreen,
    baseOrigin,
    // handlers
    onClose,
    onReady,
    onAuthToken,
    onInitiateDeposit,
    onKmsWalletsCreated,
    onLogin,
    onTransactionCompleted,
    onUnsupportedRegion,
    onSwapsCustomerSetupComplete,
    onCloseOverlay,
    onTransactionCreated,
    // auth
    auth,
    useWarnBeforeRefresh,
    // other
    overlayNode,
  } = props;

  const params: MoonPayBuyConfig['params'] = {
    apiKey,
    signature,
    currencyCode,
    defaultCurrencyCode,
    walletAddress,
    walletAddressTag,
    walletAddresses,
    walletAddressTags,
    colorCode,
    theme,
    themeId,
    language,
    baseCurrencyCode,
    baseCurrencyAmount,
    quoteCurrencyAmount,
    lockAmount,
    email,
    externalTransactionId,
    externalCustomerId,
    paymentMethod,
    redirectURL,
    showAllCurrencies,
    showOnlyCurrencies,
    showWalletAddressForm,
    unsupportedRegionRedirectUrl,
    skipUnsupportedRegionScreen,
  };

  const handlers: MoonPayBuyConfig['handlers'] = {
    onClose,
    onReady,
    onAuthToken,
    onInitiateDeposit,
    onKmsWalletsCreated,
    onLogin,
    onTransactionCompleted,
    onUnsupportedRegion,
    onSwapsCustomerSetupComplete,
    onCloseOverlay,
    onTransactionCreated,
  };

  return {
    flow: 'buy',
    variant: props.variant,
    environment,
    params,
    debug,
    baseOrigin,
    handlers,
    auth,
    useWarnBeforeRefresh,
    containerNodeSelector: `#${MOONPAY_WIDGET_CONTAINER_ID}`,
    overlayNode,
  };
};

