export const getPartnerTopupConfigFromProps = ({
  props,
  apiKey,
  debug,
  environment,
  signature,
}: ConfigConversionParams<'partnerTopup'>): MoonPayPartnerTopupConfig => {
  const {
    enabledMethods,
    cryptoAmount,
    fromDefaultCurrencyCode,
    fromChainName,
    toChainName,
    testLocation,
  } = props;

  // Partner topup widget will support all buy config params
  const buyConfigProps = getBuyConfigFromProps({
    props,
    apiKey,
    debug,
    environment,
    signature,
  });

  const params: MoonPayPartnerTopupConfig['params'] = {
    ...buyConfigProps.params,
    enabledMethods,
    cryptoAmount,
    fromDefaultCurrencyCode,
    fromChainName,
    toChainName,
    testLocation,
  };

  return {
    ...buyConfigProps,
    flow: 'partnerTopup',
    params,
  };
};
