/* eslint-disable react/destructuring-assignment */
import { useContext, useMemo } from 'react';
import type { MoonPayAccountChallengeProps } from '../types';
import { MoonPayWidget } from './MoonPayWidget';
import { MoonPayContext } from './MoonPayProvider';
import { getAccountChallengeConfigFromProps } from '../helpers';

export function MoonpayAccountChallengeWidget(
  props: MoonPayAccountChallengeProps,
) {
  const { apiKey, environment } = useContext(MoonPayContext);

  const config = useMemo(
    () =>
      getAccountChallengeConfigFromProps({
        props,
        apiKey,
        environment,
        debug: false,
        signature: '',
      }),
    [props, environment, apiKey],
  );

  return (
    <MoonPayWidget
      config={config}
      visible={props.visible}
      onUrlSignatureRequested={props.onUrlSignatureRequested}
      className={props.className}
      style={props.style}
    />
  );
}
