/* eslint-disable react/destructuring-assignment */
import { useContext, useMemo } from 'react';
import { getMoonPayBalanceWithdrawConfigFromProps } from '../helpers';
import type { MoonPayBalanceWithdrawProps } from '../types';
import { MoonPayContext } from './MoonPayProvider';
import { MoonPayWidget } from './MoonPayWidget';

export function MoonPayBalanceWithdrawWidget(
  props: MoonPayBalanceWithdrawProps,
) {
  const { environment } = useContext(MoonPayContext);

  const config = useMemo(
    () =>
      getMoonPayBalanceWithdrawConfigFromProps({
        props,
        environment,
        debug: false,
      }),
    [props, environment],
  );

  return (
    <MoonPayWidget
      config={config}
      visible={props.visible}
      onUrlSignatureRequested={props.onUrlSignatureRequested}
      className={props.className}
      style={props.style}
    />
  );
}
