import { useContext, useMemo } from 'react';
import { getPartnerTopupConfigFromProps } from '../helpers';
import type { MoonPayPartnerTopupProps } from '../types';
import { MoonPayContext } from './MoonPayProvider';
import { MoonPayWidget } from './MoonPayWidget';

export function MoonPayTopUpWidget(props: MoonPayPartnerTopupProps) {
  const { apiKey, environment } = useContext(MoonPayContext);
  const {
    visible,
    className,
    style,
    onUrlSignatureRequested,
    signature,
  } = props;

  const config = useMemo(
    () =>
      getPartnerTopupConfigFromProps({
        props,
        apiKey,
        environment,
        debug: false,
        signature: signature || '',
      }),
    [props, apiKey, environment, signature],
  );

  return (
    <MoonPayWidget
      config={config}
      onUrlSignatureRequested={onUrlSignatureRequested}
      visible={visible}
      className={className}
      style={style}
    />
  );
}
